import React, { Component } from "react";
import RegInfo from "./RegInfo";
import { colors } from "../../styles";
import RegServices from "./RegServices";
import { Fade } from "shards-react";
import { register } from "../../api";
import { Redirect, withRouter } from "react-router-dom";

export class Register extends Component {
  constructor(props) {
    super(props);
    this.state = { info: {}, services: {}, step: 1, redirectHome: false };
  }

  handleSave(e) {
    const i = this.state.info;
    let data = {
      brandName: i.brandName,
      email: i.email,
      address: i.address,
      phone: i.phone,
      typeOfBrand: i.type,
      services: e.services,
      otherServices: e.otherServices
    };
    register(data, res => {
      if (res.status === 200) {
        this.setState({ redirectHome: true });
      }
    });
  }

  render() {
    return (
      <div>
        {this.state.redirectHome && (
          <Redirect to={{ pathname: "/", state: { registered: true } }} />
        )}
        <div
          style={{
            width: "100%",
            height: "100%",
            position: "absolute",
            backgroundColor: colors.primary,
            display: "flex",
            justifyContent: "center",
            alignItems: "center"
          }}
        >
          <div
            style={{
              width: window.innerWidth < 700 ? "100%" : 960,
              backgroundColor: "white",
              boxShadow:
                "0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23)",
              padding: window.innerWidth < 700 ? 16 : 64,
              borderRadius: 8
            }}
          >
            {this.state.step === 1 ? (
              <Fade in={this.state.step === 1}>
                <RegInfo
                  location={this.props.location}
                  context={this.props.context}
                  handleSubmit={e => this.setState({ step: 2, info: e })}
                  info={this.state.info}
                />
              </Fade>
            ) : (
              <Fade in={this.state.step === 2}>
                <RegServices
                  services={this.state.services}
                  context={this.props.context}
                  handleSubmit={e => this.handleSave(e)}
                  handleBackButton={e => {
                    this.setState({ step: 1, services: e });
                  }}
                />
              </Fade>
            )}
          </div>
        </div>
      </div>
    );
  }
}

const styles = {
  box: {
    width: ""
  }
};

export default withRouter(Register);
