import React from "react";
import { generalS } from "../../generalS";
import { quickServices } from "../../services";
import ServicesCard from "./ServicesCard";

function Services(props) {
  const lang = props.context.language;
  let w = window.innerWidth;

  return (
    <div style={{ marginTop: w < 700 ? "4em" : "10em" }}>
      <div
        style={{
          // backgroundImage: "url(m-h-background-6-1.jpg)",
          width: "100%",
          // padding: "8px 0px 24px 0px",
          position: "relative"
        }}
      >
        <h2 style={{ textAlign: "center" }}>{generalS.whatWeOffer[lang]}</h2>
        <p
          style={{
            fontWeight: "bold",
            width: window.innerWidth < 700 ? "90%" : 500,
            textAlign: "center",
            margin: "0px auto",
            marginBottom: w < 700 ? 32 : 52
          }}
        >
          {generalS.ourServicesGrow[lang]}
        </p>
        <img
          src="m-h-icon-back-4.png"
          style={{ position: "absolute", top: 0, zIndex: -1 }}
        />
      </div>

      <div
        style={{
          display: "flex",
          justifyContent: w < 700 ? "flex-start" : "flex-start",
          padding: 16,

          overflowX: w < 700 ? "scroll" : "auto"
        }}
      >
        {quickServices.map(v => {
          return <ServicesCard context={props.context} data={v} />;
        })}
      </div>
    </div>
  );
}

export default Services;
