import React, { useState } from "react";
import { generalS } from "../../generalS";
import { Link } from "react-router-dom";
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "shards-react";

function Header(props) {
  const lang = props.context.language;
  let w = window.innerWidth;
  const [langOpen, setLangOpen] = useState(false);

  return (
    <div>
      <div
        style={{
          padding: "8px 8xp",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          padding: "8px 32px"
        }}
      >
        <Dropdown open={langOpen} toggle={e => setLangOpen(!langOpen)}>
          <DropdownToggle
            style={{
              backgroundColor: "white",
              border: "none",
              color: "black",
              fontWeight: "bold"
            }}
          >
            <i style={{ fontSize: 24 }} class="fa fa-language"></i>

            {/* {generalS.language[lang]} */}
          </DropdownToggle>
          <DropdownMenu>
            <DropdownItem onClick={e => props.context.setLanguage(0)}>
              کوردی
            </DropdownItem>
            <DropdownItem onClick={e => props.context.setLanguage(2)}>
              English
            </DropdownItem>
            <DropdownItem onClick={e => props.context.setLanguage(1)}>
              عربی
            </DropdownItem>
          </DropdownMenu>
        </Dropdown>
        <Link to="/">
          <img
            src="sulibblogo-dark.png"
            style={{ width: 100, margin: "0px 32px" }}
          />
        </Link>
        {/* <a
          href="https://instagram.com/sulibeautybooking"
          style={{ color: "black", border: "none", fontWeight: "bold" }}
          >
          سۆشیاڵ میدیا
        </a> */}
        <Link
          to="/register"
          style={{ color: "black", border: "none", fontWeight: "bold" }}
        >
          {generalS.register[lang]}
        </Link>
      </div>
    </div>
  );
}

export default Header;
