import React, { Component } from "react";
import { Button, ButtonGroup } from "shards-react";
import { services } from "../../services";
import { generalS } from "../../generalS";
import { colors } from "../../styles";
import { FormTextarea } from "shards-react";

import RegServicesCategories from "./RegServicesCategories";

class RegServices extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeService: "nails",
      category: services[0],
      services: this.props.services,
      otherServices: "",
      count: 0
    };
  }

  render() {
    let lang = this.props.context.language;
    return (
      <div
        style={{
          display: "flex",
          flexDirection: window.innerWidth < 700 ? "column" : "row"
        }}
      >
        <div>
          <img
            src="sulibb-register.png"
            style={{ width: window.innerWidth < 700 ? 200 : 400 }}
          />
        </div>

        <div>
          <h1
            style={{
              color: colors.primaryBold,
              marginBottom: 40,
              fontSize: 32,
              marginTop: window.innerWidth < 700 ? 32 : 0
            }}
          >
            {generalS.services[lang]}
          </h1>
          <div>
            {services.map(v => {
              return (
                <button
                  className={
                    v.name[2] === this.state.activeService
                      ? "bb-tab-button-active bb-tab-button"
                      : "bb-tab-button"
                  }
                  key={v.name[lang]}
                  onClick={e =>
                    this.setState({
                      category: v,
                      activeService: v.name[2],
                      count: ++this.state.count
                    })
                  }
                >
                  {v.name[lang]}
                </button>
              );
            })}
          </div>

          <button
            className={
              generalS.otherServices[lang] === this.state.activeService
                ? "bb-tab-button-active bb-tab-button"
                : "bb-tab-button"
            }
            key={generalS.otherServices[lang]}
            onClick={e =>
              this.setState({
                activeService: generalS.otherServices[lang],
                count: ++this.state.count
              })
            }
          >
            {generalS.otherServices[lang]}
          </button>

          {this.state.activeService !== generalS.otherServices[lang] ? (
            <div>
              <RegServicesCategories
                services={this.state.services}
                context={this.props.context}
                category={this.state.category}
                handleChange={cs => this.setState({ services: cs })}
              />
            </div>
          ) : (
            <div style={{ marginBottom: 16, marginTop: 24 }}>
              <FormTextarea
                style={{ height: 200 }}
                onChange={e => this.setState({ otherServices: e.target.value })}
              />
            </div>
          )}

          <div style={{ display: "flex", justifyContent: "flex-end" }}>
            <input
              className="bb-button-ghost"
              type="submit"
              value={generalS.back[lang]}
              onClick={e => this.props.handleBackButton(this.state.services)}
            />
            <input
              className="bb-button"
              type="submit"
              value={generalS.submit[lang]}
              onClick={e => this.props.handleSubmit(this.state)}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default RegServices;
