import React, { useState } from "react";
import { generalS } from "../../generalS";
import { Link } from "react-router-dom";
// import { Carousel } from "antd";
import Slider from "react-animated-slider";
import "react-animated-slider/build/horizontal.css";
import { Fade, Button } from "shards-react";
import { colors } from "../../styles";

export default function Cover(props) {
  const lang = props.context.language;
  let w = window.innerWidth;
  let [slide, setSlide] = useState(false);
  let [animate, setAnmiate] = useState(true);

  return (
    <div
      style={{
        position: "relative",
        display: w < 700 ? "block" : "flex",
        alignItems: "center"
      }}
    >
      {/* <Slider
        autoplay={3000}
        animateIn={"animateIn"}
        style={{ height: "300px !important" }}
      >
        {content.map((article, index) => (
          <div
            key={index}
            className="slider-content"
            style={{
              // background: `url('${article.image}') no-repeat center center`,
              display: "flex",
              justifyContent: "center",
              alignItems: "center"
              // height: 2000
            }}
          >
            <img src={article.image} style={{ width: "100%" }} />
            <div
              className="inner"
              style={{
                fontWeight: "bolder",
                position: "absolute",
                top: 24,
                margin: "0 auto"
              }}
            >
              <h2>{article.title[lang]}</h2>
              <div>{article.description[lang]}</div>
            </div>
          </div>
        ))}
      </Slider> */}

      <div
        style={{
          width: "100%",
          position: "absolute",
          display: "flex",
          justifyContent: "space-between",
          padding: "0px 16px",
          height: "50%",
          alignItems: "center"
        }}
      >
        <h1
          className="disable-select"
          style={{
            fontSize: window.innerWidth < 700 ? 80 : "120px",
            cursor: "pointer",
            color: "#3E3E3E"
          }}
          onClick={e => {
            setAnmiate(false);
            setTimeout(() => setSlide(!slide), 200);
            setTimeout(() => setAnmiate(true), 300);
          }}
        >
          &#x2039;
        </h1>
        <h1
          className="disable-select"
          style={{
            fontSize: window.innerWidth < 700 ? 80 : "120px",
            cursor: "pointer",
            color: "#3E3E3E"
          }}
          onClick={e => {
            setAnmiate(false);
            setTimeout(() => setSlide(!slide), 200);
            setTimeout(() => setAnmiate(true), 300);
          }}
        >
          &#x203A;
        </h1>
      </div>

      <Fade in={animate}>
        <div style={{}}>
          <img
            src={!slide ? "home_cover.jpg" : "home_cover_2.jpg"}
            style={{ width: "100%" }}
          />
        </div>
      </Fade>

      <div
        style={{
          position: w < 700 ? "relative" : "absolute",
          textAlign: "right",
          color: "#3e3e3e",
          padding: 16,
          width: w < 700 ? "100%" : 700,
          backgroundColor: w < 700 ? "grey" : "none",
          backgroundImage: w < 700 ? "url(m-h-background-5-1-1.jpg)" : "none",
          backgroundSize: "cover",
          right: w > 700 && "64px"
        }}
      >
        {w > 700 && (
          <img
            src="sulibblogo.png"
            style={{
              width: w < 700 ? 80 : "200px",
              margin: "0px 16px",
              marginBottom: 16
            }}
          />
        )}
        <h1
          style={{
            color: "#3e3e3e",
            marginBottom: 16,
            marginTop: w < 700 ? 24 : "auto",
            fontWeight: "bold",
            fontSize: 32
          }}
        >
          {!slide ? content[1].title[lang] : content[0].title[lang]}
        </h1>
        <p style={{ fontWeight: "bolder", fontSize: 16 }}>
          {!slide ? content[1].description[lang] : content[0].description[lang]}
        </p>
        {slide && (
          <Link to="/register">
            <input
              type="submit"
              className="bb-button"
              value={generalS.register[lang]}
            />
          </Link>
        )}
      </div>
    </div>
  );
}

const content = [
  {
    title: [
      "با ئێمە بزنزەکەت بەڕێوەبەرین",
      "دعونا إدارة عملك",
      "Let us manage your business"
    ],
    description: [
      "ئێمە یارمەتی بزنسەکەت دەدەینئێستا دەتوانن لیستی خزمەتگوزارییەکانتان تۆماربکەن بۆ ئەوەی بەردەست بێت لەسەر پلاتفۆرمەکەمان. ئێمە بەرنامەی کارگێڕیت پێدەبەخشن بۆ گێڕانەوەی کات، خەرجی و پەیوەندی تەلەفۆنی بەشی خزمەتگوزاری کڕیارەکان",
      "دعنا نساعدکم فی ادارة العمل یمکنکم تسجیل خدماتکم و سوف نتولی الحجز، نزید الکفائة من خلال برمجة الادارة التی تحتاج لنوفر علیکم وقتکم، مواردکم و اتصالات مرکز الزبائن سجل الان",
      "Sign up on our platform, list your beauty services and we will handle your bookings, provide you the management system you need to save your time, money and customer service calls."
    ],
    image: "home_cover_2.jpg",
    button: ["بەژداری کردن", "تسجيل", "Register"]
  },
  {
    title: [
      "پڕۆفیشناڵەکان لای ئێمەن",
      "اعثر علی خبراء التجمیل",
      "Find beauty professionals"
    ],
    description: [
      "ئێستا دەتوانیت بگەڕێیت بەدوای خزمەتگوزارییەکانی پڕۆفیشناڵە دڵخوازەکانی خۆت و حجزیان بکەیت لەڕێی ماڵپەڕەکەمانەوە. ",
      "یمکنکم البحث عن الخدمات اللتی تحبون عبر موقعنا، ایجاد خبرائکم المفضلین و حجز موعد.",
      "You can search in the listed beauty services to find and book your favorite professional."
    ],
    image: "home_cover.jpg",
    button: ["زیاتر", "أكثر من", "More"]
  }
];
