import React, { useState } from "react";
import { FormInput } from "shards-react";
import { Card, CardBody, CardTitle, CardSubtitle } from "shards-react";
import { getPartners } from "../../api";
import { services } from "../../services";
// import { ReactMUIDatatable } from "react-material-ui-datatable";

function Admin(params) {
  const [password, setpassword] = useState("initialState");
  const [auth, setAuth] = useState(false);
  const [data, setData] = useState([]);
  const [service, setService] = useState([]);

  function check(e) {
    e.preventDefault();
    if (password === "raceflag90") {
      getPartners(res => {
        let r = res.data;
        setData(res.data);
      });

      setAuth(true);
    }
  }

  function getServices(d, data) {
    let title = "j";
    let categories = [];
    let jsx = [];
    for (let key in data) {
      title = services[key - 1].name[2];
      for (let x in data[key]) {
        categories.push(
          " | " + services[key - 1].categories[data[key][x] - 1].name[2]
        );
      }
      jsx.push(
        <div>
          <p style={{ fontWeight: "bold", marginBottom: 4, color: "red" }}>
            {title}
          </p>
          <p style={{ marginBottom: 16 }}>{categories}</p>
        </div>
      );
      categories = [];
    }

    return jsx;
  }

  return (
    <div style={{ padding: 64 }}>
      <div>
        {auth === false ? (
          <form onSubmit={e => check(e)}>
            <FormInput onChange={e => setpassword(e.target.value)} />
          </form>
        ) : (
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              flexWrap: "wrap"
            }}
          >
            {data.map(d => {
              return (
                <Card style={{ width: 300, margin: 8, fontSize: "1.2em" }}>
                  <CardBody>
                    <CardTitle>{d.brandName}</CardTitle>
                    <CardSubtitle>{d.typeOfBrand}</CardSubtitle>
                    <div>
                      <div>
                        <p style={{ marginBottom: 0 }}>Phone: {d.phone}</p>
                        <p style={{ marginBottom: 0 }}>Address: {d.address}</p>
                      </div>

                      <div style={{ marginTop: 16 }}>
                        {getServices(d, d.services)}
                      </div>
                      <p style={{ marginBottom: 0, color: "blue" }}>
                        {d.otherServices}
                      </p>
                    </div>
                  </CardBody>
                </Card>
              );
            })}
          </div>
        )}
      </div>
    </div>
  );
}

export default Admin;
