import Axios from "axios";
import React, { Component } from "react";
// import Cookies from "universal-cookie";

// const cookies = new Cookies();
// cookies.set("language", 2, { path: "/" });

export const MyContext = React.createContext();

export default class MyProvider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      language: 0,
      authenticated: false,
      session: {},

      setLanguage: langVal => {
        this.setState({ language: langVal });
      }
    };
  }

  render() {
    return (
      <MyContext.Provider
        value={{
          state: this.state
        }}
      >
        {this.props.children}
      </MyContext.Provider>
    );
  }
}
