import React, { useState } from "react";
import { generalS } from "../../generalS";
import { Button, Modal, ModalBody, ModalHeader } from "shards-react";

function ServicesCard(props) {
  const lang = props.context.language;
  let w = window.innerWidth;
  const [registered, setRegistered] = useState(false);

  return (
    <div
      style={{
        borderRadius: 8,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: 220,
        padding: 24,
        boxShadow: "0 4px 6px -1px rgba(0, 0, 0, 0.1)",
        margin: "0px 8px",
        paddingTop: 0
      }}
    >
      <Modal open={registered} toggle={e => setRegistered(false)}>
        <ModalHeader style={{ margin: "0px auto" }}>
          {generalS.comingSoon[lang]}
        </ModalHeader>
        <ModalBody>{generalS.thanksForTesting[lang]}</ModalBody>
      </Modal>

      <div
        style={{
          width: 220,
          height: 220,
          marginBottom: 16,
          display: "flex",
          justifyContent: "center",
          alignItems: "center"
        }}
      >
        <img style={{ width: "100%", height: "100%" }} src={props.data.src} />
      </div>
      <h4>{props.data.title[lang]}</h4>
      {/* <p>{props.data.description[lang]}</p> */}
      <p>
        {generalS.serviceCardPre1[lang]} {props.data.title[lang]}{" "}
        {generalS.serviceCardPre2[lang]}
      </p>
      <input
        className="bb-button"
        value={generalS.booknow[lang]}
        type="submit"
        onClick={e => setRegistered(true)}
      />
    </div>
  );
}

export default ServicesCard;
