import React, { Component } from "react";
import { FormCheckbox } from "shards-react";

export class RegServicesCategories extends Component {
  constructor(props) {
    super(props);
    this.state = { actives: {} };
  }

  componentDidMount() {
    this.setState({ actives: this.props.services });
    console.log(this.props.services);
  }

  handleCheckboxChange(v) {
    let c = this.props.category;
    let cs = JSON.parse(JSON.stringify(this.state.actives));

    console.log(v.id);
    if (cs[c.id]) {
      let flag = false;
      for (let i = 0; i < cs[c.id].length; i++) {
        const element = cs[c.id][i];
        if (element === v.id) {
          cs[c.id].splice(i, 1);
          flag = true;
          break;
        }
      }
      if (!flag) cs[c.id].push(v.id);
    } else cs[c.id] = [v.id];

    this.setState({ actives: cs });
    this.props.handleChange(cs);
  }

  render() {
    let lang = this.props.context.language;
    let c = this.props.category;
    return (
      <div
        style={{
          display: "flex",
          justifyContent: "flex-end",
          minHeight: window.innerWidth < 700 ? "auto" : 200,
          margin: window.innerWidth < 700 ? "32px 24px" : "32px 0px"
        }}
      >
        <div>
          {this.props.category &&
            this.props.category.categories &&
            this.props.category.categories.map(v => {
              return (
                <FormCheckbox
                  checked={
                    this.state.actives[c.id] &&
                    this.state.actives[c.id].includes(v.id)
                  }
                  onChange={e => this.handleCheckboxChange(v)}
                >
                  {v.name[lang]}
                </FormCheckbox>
              );
            })}
        </div>
      </div>
    );
  }
}

export default RegServicesCategories;
