import React from "react";
import { generalS } from "../../generalS";

function GetOurApp(props) {
  const lang = props.context.language;
  let w = window.innerWidth;

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center"
      }}
    >
      <img
        style={{ width: "100%" }}
        src={w < 700 ? "m-h-background2.jpg" : "m-h-background-2-2.jpg"}
      />
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
          position: "absolute"
        }}
      >
        <div style={{ marginRight: window.innerWidth > 700 && 180 }}>
          <a
            style={{ textDecoration: "underline" }}
            href="https://qrgo.page.link/bd1xh"
          >
            <h2 style={{ marginBottom: 32, width: 300, fontWeight: "bolder" }}>
              {generalS.getOurApps[lang]}
            </h2>
          </a>
          <img
            src="SuliBBmvp.png"
            style={{ width: window.innerWidth < 700 ? 80 : 120 }}
          />
        </div>
        {window.innerWidth > 700 && (
          <img
            src="app-mockup.png"
            style={{ position: "absolute", right: -220 }}
          />
        )}
        {/* <div style={{ display: "flex" }}>
          <a href="" style={{ margin: 8 }}>
            <img
              src="get-it-on-android.png"
              style={{ width: w < 700 ? 160 : 200 }}
            />
          </a>
          <a href="" style={{ margin: 8 }}>
            <img src="Download_on_the_App_Store.png" style={{ width: w < 700 ? 160 : 200 }} />
          </a>
        </div> */}
      </div>
    </div>
  );
}

export default GetOurApp;
