import React, { Component } from "react";
import { generalS } from "../../generalS";
import { colors } from "../../styles";
import { FormRadio } from "shards-react";

class RegInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      type: "ENTERPRISE",
      brandName: "",
      email: "",
      phone: null,
      address: ""
    };
  }

  componentDidMount() {
    let p = this.props.info;
    this.setState({
      type: p.type,
      brandName: p.brandName,
      email: p.email,
      phone: p.phone,
      address: p.address
    });
  }

  submit(e) {
    e.preventDefault();
    this.props.handleSubmit(this.state);
  }

  render() {
    let lang = this.props.context.language;
    const s = this.state;
    return (
      <div
        style={{
          display: "flex",
          flexDirection: window.innerWidth < 700 ? "column" : "row"
        }}
      >
        <div>
          <img
            src="sulibb-register.png"
            style={{ width: window.innerWidth < 700 ? 200 : 400 }}
          />
        </div>

        <div className="register">
          <h1
            style={{
              color: colors.primaryBold,
              marginBottom: 40,
              fontSize: 32,
              marginTop: window.innerWidth < 700 ? 32 : 0
            }}
          >
            {generalS.register[lang]}
          </h1>
          <form onSubmit={e => this.submit(e)}>
            <ul
              style={{
                display: "flex",
                flexDirection: "column",
                listStyle: "none",
                padding: 0
              }}
            >
              <li style={styles.li}>
                <input
                  required
                  value={s.brandName}
                  onChange={e => this.setState({ brandName: e.target.value })}
                  style={styles.input}
                  type="name"
                  placeholder={generalS.name[lang]}
                />
              </li>
              <li style={styles.li}>
                <input
                  required
                  value={s.email}
                  onChange={e => this.setState({ email: e.target.value })}
                  style={styles.input}
                  type="email"
                  placeholder={generalS.email[lang]}
                />
              </li>
              <li style={styles.li}>
                <input
                  required
                  value={s.address}
                  onChange={e => this.setState({ address: e.target.value })}
                  style={styles.input}
                  type="address"
                  placeholder={generalS.address[lang]}
                />
              </li>
              <li style={styles.li}>
                <input
                  required
                  value={s.phone}
                  onChange={e => this.setState({ phone: e.target.value })}
                  style={styles.input}
                  type="number"
                  placeholder={generalS.phone[lang]}
                />
              </li>
            </ul>
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              <FormRadio
                inline
                name="INDIVIDUAL"
                checked={this.state.type === "INDIVIDUAL"}
                onChange={() => {
                  this.setState({ type: "INDIVIDUAL" });
                }}
              >
                {generalS.individual[lang]}
              </FormRadio>

              <FormRadio
                inline
                name="ENTERPRISE"
                checked={this.state.type === "ENTERPRISE"}
                onChange={() => {
                  this.setState({ type: "ENTERPRISE" });
                }}
              >
                {generalS.enterprise[lang]}
              </FormRadio>
            </div>

            <div
              style={{
                display: "flex",
                justifyContent: "flex-end",
                margin: "0 20px",
                marginTop: "20px"
              }}
            >
              <input
                className="bb-button"
                type="submit"
                value={generalS.next[lang]}
              />
            </div>
          </form>
        </div>
      </div>
    );
  }
}

const styles = {
  input: {
    margin: "0px 10px",
    backgroundColor: "#F7F7F7",
    borderRadius: "30px",
    width: window.innerWidth < 700 ? "90%" : 330,

    outline: "none",
    border: "none",
    height: 50,
    padding: "0px 32px",
    color: "rgb(109, 109, 109)",
    marginBottom: 16,
    boxSizing: "borderBox",
    fontSize: 14,
    fontFamily: "inherit",
    direction: "rtl"
  },

  li: {
    direction: "rtl"
    // display: "flex"
  }
};

export default RegInfo;
