import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";
import Register from "./register/Register";
import Home from "./home/Home";
import Footer from "./main/Footer";
import Header from "./main/Header";
import PrivacyPolicy from "./main/PrivacyPolicy";
import Admin from "./main/Admin";

function Routes(props) {
  return (
    <BrowserRouter>
      <div>
        <Header context={props.context} />
        <Switch>
          <Route
            exact
            path="/register"
            render={routeProps => (
              <Register context={props.context} {...routeProps} />
            )}
          />
          <Route
            exact
            path="/"
            render={routeProps => (
              <Home context={props.context} {...routeProps} />
            )}
          />
          <Route
            exact
            path="/privacypolicy"
            render={routeProps => (
              <PrivacyPolicy context={props.context} {...routeProps} />
            )}
          />
          <Route
            exact
            path="/au-91"
            render={routeProps => (
              <Admin context={props.context} {...routeProps} />
            )}
          />
        </Switch>
        <Footer context={props.context} />
      </div>
    </BrowserRouter>
  );
}

export default Routes;
