import React, { Component } from "react";
import { Button, Modal, ModalBody, ModalHeader } from "shards-react";
import { generalS } from "../../generalS";
import Cover from "./Cover";
import GetOurApp from "./GetOurApp";
import Services from "./Services";

export class Home extends Component {
  constructor(props) {
    super(props);
    this.state = { registered: false };
  }

  componentDidMount() {
    if (
      this.props.location &&
      this.props.location.state &&
      this.props.location.state.registered
    ) {
      this.setState({ registered: true });
      this.props.history.replace({
        pathname: this.props.location.pathname,
        state: {}
      });
    }
  }

  render() {
    let lang = this.props.context.language;
    return (
      <div>
        <Modal
          open={this.state.registered}
          toggle={e => this.setState({ registered: false })}
        >
          <ModalHeader>{generalS.thankYouForRegistering[lang]} 👋 </ModalHeader>
          <ModalBody>{generalS.weWillGetBackToYouSoon[lang]}</ModalBody>
        </Modal>

        <Cover context={this.props.context} />
        <Services context={this.props.context} />
        <GetOurApp context={this.props.context} />
      </div>
    );
  }
}
export default Home;
