import React from "react";
import { generalS } from "../../generalS";
import { Link } from "react-router-dom";

function Footer(props) {
  const lang = props.context.language;
  let w = window.innerWidth;

  return (
    <div>
      <div
        style={{
          backgroundColor: "black",
          padding: "120px 32px",
          display: "flex",
          flexDirection: w < 700 ? "column" : "row",
          justifyContent: "space-around",
          alignItems: "center",
          color: "white"
        }}
      >
        <div style={{ textAlign: w < 700 ? "center" : "left", width: 300 }}>
          <h4 style={{ color: "white" }}>{generalS.contact[lang]}</h4>
          <p style={{ margin: "4px 0px" }}>
            {generalS.address[lang]}: Five One Labs, Salim St. HC66+9W
            Sulaymaniyah
          </p>
          <p style={{ margin: "4px 0px" }}>
            {generalS.email[lang]}:{" "}
            <span style={{ direction: "rtl" }}>sulibb.service@outlook.com</span>
          </p>
          <p style={{ margin: "4px 0px" }}>
            {generalS.phone[lang]}: {generalS.bbPhone[lang]}
          </p>
        </div>

        <div
          style={{
            textAlign: "center",
            width: w < 700 ? "100%" : "30%",
            margin: w < 700 ? "32px 0px" : 0
          }}
        >
          <Link to="/">
            <img
              style={{ width: 200, marginBottom: 32 }}
              src="sulibblogo.png"
            />
          </Link>
          <p>{generalS.coverDescriptions[lang]}</p>
          <p style={{ marginTop: 32, marginBottom: 8 }}>
            {generalS.language[lang]}
          </p>
          <div>
            <div
              style={{
                padding: 4,
                // border: "1px solid white",
                cursor: "pointer",
                display: "inline-block"
              }}
              onClick={e => props.context.setLanguage(0)}
            >
              کوردی
            </div>
            <div
              style={{
                padding: 4,
                // border: "1px solid white",
                cursor: "pointer",
                display: "inline-block",
                margin: "0px 8px"
              }}
              onClick={e => props.context.setLanguage(2)}
            >
              English
            </div>
            <div
              style={{
                padding: 4,
                // border: "1px solid white",
                cursor: "pointer",
                display: "inline-block"
              }}
              onClick={e => props.context.setLanguage(1)}
            >
              عربی
            </div>
          </div>
        </div>
        <div
          style={{
            textAlign: w < 700 ? "center" : "right",
            display: "flex",
            flexDirection: "column",
            color: "white",
            width: 300
          }}
        >
          <h4 style={{ color: "white" }}>{generalS.links[lang]}</h4>
          <Link to="/register" style={{ color: "white" }}>
            {generalS.register[lang]}
          </Link>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: window.innerWidth < 700 ? "center" : "flex-end"
            }}
          >
            <a
              target="_blank"
              style={{ margin: "8px 0px", color: "white" }}
              href="https://instagram.com/sulibeautybooking"
            >
              <img src="ig-icon.jpg" style={{ width: 22 }} />
            </a>
            <a
              target="_blank"
              style={{ margin: "8px 0px", color: "white" }}
              href="https://www.facebook.com/Sulibb-109611187125164"
            >
              <img src="fb-icon.png" style={{ width: 32, margin: "0px 8px" }} />
            </a>
            <a
              target="_blank"
              style={{ margin: "8px 0px", color: "white" }}
              href="https://www.linkedin.com/company/sulibb/"
            >
              <img src="li-icon.png" style={{ width: 21 }} />
            </a>
          </div>
          <Link to="/privacypolicy" style={{ color: "white" }}>
            Privacy Policy
          </Link>
        </div>
      </div>
      <div
        style={{
          backgroundColor: "rgb(21, 21, 21)",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          padding: 0,
          margin: 0,
          color: "rgb(216, 216, 216)"
        }}
      >
        <p
          style={{ padding: 0, margin: "16px 0px", fontFamily: "englishfont" }}
        >
          created with 🖤 by{" "}
          <a
            target="_blank"
            style={{ color: "rgb(216, 216, 216)" }}
            href="https://www.instagram.com/illuba_creative"
          >
            <i>illuba</i>
          </a>
        </p>
      </div>
    </div>
  );
}

export default Footer;
